  --linear-ligh-to-dark: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --linear-dark-to-ligh: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --primary-font: "Roboto", sans-serif;
  --transition-dur-and-fun: 250ms cubic-bezier(.4, 0, .2, 1);
