}

@media screen and (min-width: 768px) {
  .theme__switch {
    width: 18px;
    height: 18px;
  }
}

.switchLight {
  animation: switchLightAnim .5s cubic-bezier(.215, .61, .355, 1) forwards;
}

.switchDark {
