.mobile-backdrop {
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  background: rgba(255, 255, 255, .2);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .mobile-backdrop {
    display: none;
  }
}

.mobile-backdrop.is-open {
  opacity: 1;
  pointer-events: all;
  overflow: auto;
}

.mobile-nav {
  width: 60%;
  height: 100vh;
  opacity: 0;
  background-color: #111;
  margin: 0;
  padding-top: 8px;
  padding-left: 20px;
  transform: translateY(-100%);
}

.mobile-backdrop.is-open .mobile-nav {
  animation: mobile-navAnim .5s cubic-bezier(.215, .61, .355, 1) forwards;
}

.mobile__logo {
  opacity: 0;
  margin-bottom: 40px;
  transform: translateX(-100%)scale(0);
}

.mobile-backdrop.is-open .mobile__logo {
  animation: mobile-menuAnim .5s cubic-bezier(.215, .61, .355, 1) .5s both;
}

.mobile-menu {
  opacity: 0;
  margin: 0;
  padding: 0;
  transform: translateX(-100%)scale(0);
}

.mobile-backdrop.is-open .mobile-menu {
  animation: mobile-menuAnim .5s cubic-bezier(.215, .61, .355, 1) .5s both;
}

.mobile-menu__item:not(:last-child) {
  margin-bottom: 24px;
}

.mobile-menu__link {
  color: currentColor;
}

@keyframes mobile-navAnim {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes mobile-menuAnim {
  0% {
    opacity: 0;
    transform: translateX(-100%)scale(0);
  }

  100% {
    opacity: 1;
    transform: translateX(0)scale(1);
  }
}

@keyframes switchLightAnim {
  0% {
    transform: translate(-15%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-190%, -50%)rotate(-180deg);
  }
}

@keyframes switchDarkAnim {
  0% {
    transform: translate(-190%, -50%)rotate(-180deg);
  }

  100% {
    transform: translate(-15%, -50%)rotate(0deg);
  }
}

@media screen and (min-width: 768px) {
  @keyframes switchLightAnim {
    0% {
      transform: translate(-15%, -50%)rotate(0deg);
    }

    100% {
      transform: translate(-215%, -50%)rotate(-180deg);
    }
  }

  @keyframes switchDarkAnim {
    0% {
      transform: translate(-215%, -50%)rotate(-180deg);
    }

    100% {
      transform: translate(-15%, -50%)rotate(0deg);
    }
  }
}















